import { useEffect, useState } from 'react';
import style from './Slideshow.module.css'
import getData from '../../api/dataFetch';

function Slideshow(props) {
	const [imgUrl, setImgUrl] = useState([])
	const [imgStyle, setImgStyle] = useState([])
	const [dotStyle, setDotStyle] = useState([]) 
	const [slideIndex, setSlideIndex] = useState(1)
	
	const imgStyleTemp = [ 
		[style.mySlides, style.fade], 
		[style.mySlides, style.fade], 
		[style.mySlides, style.fade]
	] 
	
	const dotStyleTemp = [ 
		[style.dot], 
		[style.dot], 
		[style.dot]
	]


	
	function plusSlides(n) { 
		console.log(slideIndex, n)
		setSlideIndex(slideIndex + n)
	} 
	
	function currentSlide(n) { 
		showSlides(setSlideIndex(n)); 
	} 
	
	function showSlides(n) {
		let index = slideIndex;
		if (n > imgStyleTemp.length) {
			setSlideIndex(1)
			index = 1
		}
		if (n < 1)  { 
			setSlideIndex(imgStyle.length) 
			index = imgStyle.length
		}

		for(let i = 0; i < imgStyleTemp.length; i++) { 
			if (!imgStyleTemp[i].includes(style.mySlides_hide))  { 
				imgStyleTemp[i].push(style.mySlides_hide)
			}
		}

		let a = []
		imgStyleTemp[index - 1].pop(); 
		for(var i = 0; i < imgStyleTemp.length; i++) {
			a.push(imgStyleTemp[i].join(' '))
		}

		setImgStyle(a)

		for (let i = 0; i < dotStyleTemp.length; i++) {
			dotStyleTemp[i] = dotStyleTemp[i].filter(el => el !== style.active)
		}

		dotStyleTemp[index -1].push(style.active);

		let dots = []
		for(var i = 0; i < dotStyleTemp; i++) {
			dots.push(dotStyleTemp[i].join(' '))
		}

		setDotStyle(dots) 
	}

	
	let first = false;
	useEffect(() => {
		let skip = Math.floor(Math.random() * 100)
		let scenicQuery = `v2/Tourism/ScenicSpot?$top=${3}&$skip=${skip}&$filter=Picture/PictureUrl1 ne null&$format=JSON`

		if (first === false) {
			first = false; 
			getData(scenicQuery)
			.then(res => { 
				setImgUrl(res.map(i => i.Picture.PictureUrl1)) 
				showSlides(slideIndex) 
		})

		if (first === true) {
			const time = setInterval(() => { 
				plusSlides(1) 
			}, (2000)); 
		}

		}
	}, [slideIndex, first])


	return (
		<div className={style.container}>
			<div className={imgStyle[0]}>
			<img className={style.img} src={imgUrl[0]} alt="" />
			</div>
			<div className={imgStyle[1]}>
			<img className={style.img} src={imgUrl[1]} alt="" />
			</div>
			<div className={imgStyle[2]}>
			<img className={style.img} src={imgUrl[2]} alt="" />
			</div>

			<a className={style.prev} onClick={() => plusSlides(-1)}>&#10094;</a>
			<a className={style.next} onClick={() => plusSlides(1)}>&#10095;</a>

			<div style={{text_align: 'center'}}>
				<span className={dotStyle[0]} onClick={() => currentSlide(1)}></span>
				<span className={dotStyle[1]} onClick={() => currentSlide(2)}></span>
				<span className={dotStyle[2]} onClick={() => currentSlide(3)}></span>
			</div>
		</div>
	)
}

export default Slideshow;